.vh100 {
  min-height: 100vh;
}

a {
  text-decoration: none !important;
}

.h-4 {
  width: 1rem !important;
}

.w-4 {
  width: 1rem !important;
}

.hire-me-button {
  border-radius: 3px;
  padding: 0.3rem 0.5rem !important;
}

.right-icon {
  margin-bottom: 2px !important;
}

.item-button-active {
  color: #fff !important;
  background-color: #565e64 !important;
  border-color: #51585e !important;
}

.skill-graph {
  width: 600px;
}

.skills-container {
  width: 100%;
  background: #f4f4f4;
  margin-bottom: 10px;
}

.skills {
  text-align: left;
  padding: 10px 10px 10px 0px;
}

.skill-background {
  background: rgba(200, 200, 200, 0.9);
  padding: 10px;
  margin-top: -10px;
  margin-bottom: -10px;
  text-align: center;
  display: inline-block;
  width: 100px;
}

.percent {
  float: right;
}

.skills {
  background: #ddd;
}

.html {
  width: 70%;
}

.css {
  width: 65%;
}

.c-sharp {
  width: 30%;
}

.php {
  width: 40%;
}

.js,
.react {
  width: 60%;
}

.text-background {
  color: #333;
  text-decoration: none !important;
  background: #f4f4f4;
}

.paragraph > a {
  color: #333 !important;
}

.profile-background {
  color: #f4f4f4 !important;
  border-radius: 50px;
}

.profile-image {
  width: 200px;
  height: 200px;
  background: #f4f4f4;
  border-radius: 100px;
  margin: auto;

  background-image: url("./assets/dayne.PNG");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: -30px;
}
